<template>
  <div>
    <div v-if="paginatedData.length > 0">
      <b-table hover striped :items="paginatedData" :fields="fields" @row-clicked="goDetail"></b-table>
    </div>
    <div v-else>등록된 공지 사항이 없습니다.</div>

    <div class="btn-cover">
      <button :disabled="pageNum === 0" @click="prevPage" class="page-btn">
        이전
      </button>
      <span class="page-count">{{ pageNum + 1 }} / {{ pageCount }} 페이지</span>
      <button :disabled="pageNum >= pageCount - 1" @click="nextPage" class="page-btn">
        다음
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'paginated-list',
  data () {
    return {
      pageNum: 0,
      fields:[
        {key:"no", label:"질문 번호", tdClass: 'td1'},{key:"userid", label:"사용자", tdClass: 'td2'},
        {key:"status", label:"답변 상태", tdClass: 'td3'},
      ],              //table의 header에 표시되는 데이터 
      key:"all",
    }
  },
  props: {
    listArray: {
      type: Array,
      required: true
    },
    pageSize: {
      type: Number,
      required: false,
      default: 10
    }
  },
  methods: {
    nextPage () {
      this.pageNum += 1;
    },
    prevPage () {
      this.pageNum -= 1;
    },
    goDetail(item){   //table에서 클릭된 row의 데이터가 인자로 전달됨
      this.$router.push({name:"AQnaDetailView", query:{no:item.no}})
    },
  },
  computed: {
    pageCount () {
      let listLeng = this.listArray.length,
          listSize = this.pageSize,
          page = Math.floor(listLeng / listSize);
      if (listLeng % listSize > 0) page += 1;
      
      /*
      아니면 page = Math.floor((listLeng - 1) / listSize) + 1;
      이런식으로 if 문 없이 고칠 수도 있다!
      */
      return page;
    },
    paginatedData () {
      const start = this.pageNum * this.pageSize,
            end = start + this.pageSize;
      return this.listArray.slice(start, end);
    }
  }
}
</script>