<template>
  <div>
    <div class="row">
      <div class="col-2"></div>

      <div class="col-2">
          <b-form-select v-model='key' :options='keys'></b-form-select>
      </div>
      <div class="col-4">
        <b-form-input v-model="word" />
      </div>
      <div class="col-2">
        <b-button variant="primary"  @click='searchQna'>검색</b-button>
      </div>
      <div class="col-2"></div>
    </div>
    <div v-if="qnas.length > 0">
      <b-table hover striped :items="qnas" :fields="fields" @row-clicked="goDetail"></b-table>
    </div>
    <div v-else>등록된 질문 목록이 없습니다.</div>
  </div>
</template>


<script>
import http from "@/api/http";
export default {
  data() {
    return {
      book: Object,
    };
  },
  created() {
    //DOM객체와 연결되었고 Vue의 속성들도 초기화 된 상태
    this.isbn = this.$route.query.isbn;
    http.get(`/book/${this.isbn}`).then(({data}) =>
    {this.book = data;}
    );
  },
  methods: {
    updateHandler() {
      //데이타 검증
      let err = false;
      let msg = "";
      !this.book.isbn &&
        ((msg = "책 번호를 입력해주세요"),
        (err = true),
        this.$refs.isbn.focus());
      !err & !this.book.title &&
        ((msg = "책 제목을 입력해주세요"),
        (err = true),
        this.$refs.title.focus());
      !err & !this.book.author &&
        ((msg = "책 제목을 입력해주세요"),
        (err = true),
        this.$refs.author.focus());
      !err & !this.book.price &&
        ((msg = "책 제목을 입력해주세요"),
        (err = true),
        this.$refs.price.focus());
      if (err) {
        alert(msg);
      } else {
          http.put("/book", this.book).then(({data}) => {
          let msg = "수정 처리 중 문제 발생!!!";
          if(data === "success") msg = "수정 성공!!!";
          alert(msg);
          this.$router.push({
            name: "BookDetailView",
            query: { isbn: this.book.isbn },
          });
        });       
      }
    },
  },
};
</script>

