<template>
  <div>
    <table class="table table-bordered">
      <tr>
        <th>공지 번호</th>
        <td v-text="notice.no"></td>
      </tr>
      <tr>
        <th>제목</th>
        <td v-text="notice.subject"></td>
      </tr>
      <tr>
        <th>글쓴이</th>
        <td v-text="notice.id"></td>
      </tr>
      
      <tr>
        <th colspan="2">내용</th>
      </tr>
      <tr>
        <!-- <textarea colspan="2" name="" id="" cols="80" rows="10" v-model="notice.content"></textarea> -->
        <td colspan="2" name="" id="" cols="80" rows="10" v-html="notice.content"></td>
      </tr>

      <tr>
        <td colspan="2">
          <!-- <b-button variant="primary m-2" @click="answerModify">공지 수정</b-button> -->
          <b-button variant="primary m-2" @click="moveHandler">목록</b-button>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import http from '@/api/http';

export default {
  data() {
    return {
      no: '',
      notice: {},
    };
  },
  created() {
    //DOM객체와 연결되었고 Vue의 속성들도 초기화 된 상태
    this.no = this.$route.query.no;
    console.log('view.html..................isbn]', this.$route);
    http.get(`notice/${this.no}`).then(({ data }) => {
      console.log(data);
      this.notice = data;
    });
    // .catch(({ data }) => {
    //   alert(data);
    // });
  },
  methods: {
    moveHandler() {
      this.$router.push({ name: 'NoticeListView' });
    },
    
    // answerModify() {
    //   console.log("수정하기");
    //   http.put("notice", this.notice).then(({data})=>{alert(data)});
    // }
  },
};
</script>

<style scoped>
  th {
  width: 30%;
}
td {
  width: 70%;
}
