import Vue from "vue";
import VueRouter from "vue-router";
// import HomeView from "@/views/HomeView";
// import LoginView from "@/views/LoginView";
// import BookView from "@/views/BookView";
// import BookListView from "@/components/book/BookListView";
// import BookRegistView from "@/components/book/BookRegistView";
// import BookDetailView from "@/components/book/BookDetailView";
// import BookModifyView from "@/components/book/BookModifyView";
import QnaView from "@/views/QnaView";
import QnaListView from "@/components/qna/QnaListView";
import QnaRegistView from "@/components/qna/QnaRegistView";
import QnaDetailView from "@/components/qna/QnaDetailView";
import QnaModifyView from "@/components/qna/QnaModifyView";

import NoticeView from "@/views/NoticeView";
import NoticeListView from "@/components/notice/NoticeListView";
import NoticeRegistView from "@/components/notice/NoticeRegistView";
import NoticeDetailView from "@/components/notice/NoticeDetailView";
import NoticeModifyView from "@/components/notice/NoticeModifyView";

import AQnaView from "@/views/AQnaView";
import AQnaListView from "@/components/aqna/AQnaListView";
import AQnaRegistView from "@/components/aqna/AQnaRegistView";
import AQnaDetailView from "@/components/aqna/AQnaDetailView";
import AQnaModifyView from "@/components/aqna/AQnaModifyView";

import ANoticeView from "@/views/ANoticeView";
import ANoticeListView from "@/components/anotice/ANoticeListView";
import ANoticeRegistView from "@/components/anotice/ANoticeRegistView";
import ANoticeDetailView from "@/components/anotice/ANoticeDetailView";
import ANoticeModifyView from "@/components/anotice/ANoticeModifyView";

Vue.use(VueRouter);

const routes = [
  // {
  //   path: "/",
  //   name: "home",
  //   component: NoticeView,
  // },
  {
    path: "/qna",
    name: "qna",
    component: QnaView,
    redirect: "/qna/qnalist",
    children: [
      {
        path: "qnalist",
        name: "QnaListView",
        component: QnaListView,
      },
      {
        path: "qnaregist",
        name: "QnaRegistView",
        component: QnaRegistView,
      },
      {
        path: "qnadetail",
        name: "QnaDetailView",
        component: QnaDetailView,
      },
      {
        path: "qnamodify",
        name: "QnaModifyView",
        component: QnaModifyView,
      },
    ],
  },
  {
    path: "/notice",
    name: "notice",
    component: NoticeView,
    redirect: "/notice/noticelist",
    children: [
      {
        path: "noticelist",
        name: "NoticeListView",
        component: NoticeListView,
      },
      {
        path: "noticeregist",
        name: "NoticeRegistView",
        component: NoticeRegistView,
      },
      {
        path: "noticedetail",
        name: "NoticeDetailView",
        component: NoticeDetailView,
      },
      {
        path: "noticemodify",
        name: "NoticeModifyView",
        component: NoticeModifyView,
      },
    ],
  },
  {
    path: "/aqna",
    name: "aqna",
    component: AQnaView,
    redirect: "/aqna/aqnalist",
    children: [
      {
        path: "aqnalist",
        name: "AQnaListView",
        component: AQnaListView,
      },
      {
        path: "aqnaregist",
        name: "AQnaRegistView",
        component: AQnaRegistView,
      },
      {
        path: "aqnadetail",
        name: "AQnaDetailView",
        component: AQnaDetailView,
      },
      {
        path: "aqnamodify",
        name: "AQnaModifyView",
        component: AQnaModifyView,
      },
    ],
  },
  {
    path: "/anotice",
    name: "anotice",
    component: ANoticeView,
    redirect: "/anotice/anoticelist",
    children: [
      {
        path: "anoticelist",
        name: "ANoticeListView",
        component: ANoticeListView,
      },
      {
        path: "anoticeregist",
        name: "ANoticeRegistView",
        component: ANoticeRegistView,
      },
      {
        path: "anoticedetail",
        name: "ANoticeDetailView",
        component: ANoticeDetailView,
      },
      {
        path: "anoticemodify",
        name: "ANoticeModifyView",
        component: ANoticeModifyView,
      },
    ],
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
