<template>
  <div>
    <table class="table table-bordered">
      <tr>
        <th>질문 번호</th>
        <td v-text="qna.no"></td>
      </tr>
      <tr>
        <th>글쓴이</th>
        <td v-text="qna.userid"></td>
      </tr>
      <tr>
        <th>내용</th>
        <td v-text="qna.question"></td>
      </tr>      

      <tr>
        <th colspan="2">답변</th>
      </tr>
      <tr>
        <td colspan="2" v-if="qna.answer == ''">
          답변이 없습니다.
        </td>
        <td v-else colspan="2">
          <pre v-text="qna.answer"></pre>
        </td>
      </tr>

      <tr>
        <th colspan="2">답변</th>
      </tr>
      <tr>
        <td colspan="2">
        <textarea name="" id="" cols="80" rows="10" v-model="qna.answer"></textarea>
        </td>
      </tr>

      <tr>
        <td colspan="2">
          <b-button variant="primary m-2" @click="answerModify">답변달기</b-button>
          <b-button variant="primary m-2" @click="moveHandler">목록</b-button>
          <!-- <router-link :to="{ name: 'QnaModifyView', query: { qna: qna.no } }">
            <b-button variant="primary m-2">수정</b-button>
          </router-link> -->
          <b-button variant="primary m-2" @click="removeHandler">삭제</b-button>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import http from '@/api/http';

export default {
  data() {
    return {
      no: '',
      qna: {},
    };
  },
  created() {
    //DOM객체와 연결되었고 Vue의 속성들도 초기화 된 상태
    this.no = this.$route.query.no;
    console.log('view.html..................isbn]', this.$route);
    http.get(`qna/${this.no}`).then(({ data }) => {
      console.log(data);
      this.qna = data;
    });
    // .catch(({ data }) => {
    //   alert(data);
    // });
  },
  methods: {
    moveHandler() {
      this.$router.push({ name: 'AQnaListView' });
    },
    removeHandler() {
      console.log('view.html..................삭제]', this.no);
      http.delete(`qna?no=${this.no}`).then(({ data }) => {
        if (data == 'success') {
          alert('삭제성공');
          this.moveHandler();
        }
      });
      // .catch(({ data }) => {
      //   alert(data);
      // });
    },
    answerModify() {
      let err = false;
      let msg = "";

      if (this.qna.answer == "") {
        err = true;
        msg = "내용을 입력해 주세요";
      }

      if (err) {
        alert(msg);
      } else {
        console.log("수정하기");
        http.put("qna", this.qna).then(({data})=>{alert(data)});
      }

      
    }
  },
};
</script>

<style scoped>
  th {
    width: 30%;
  }
  td {
    width: 70%;
  }
  textarea {
    width: 100%;
		height: 200px;
		padding: 10px;
		box-sizing: border-box;
		/* border: solid 2px #1E90FF; */
		border-radius: 5px;
    border: none;
		/* font-size: 16px; */
		resize: none;
  }
</style>
