<template>
  <div>
    <div class="row">
      <div class="col-2"></div>

      <div class="col-2">
          <b-form-select v-model='key' :options='keys'></b-form-select>
      </div>
      <div class="col-4">
        <b-form-input v-model="word" />
      </div>
      <div class="col-2">
        <b-button variant="primary"  @click='searchNotice'>검색</b-button>
      </div>
      <div class="col-2"></div>
    </div>
    <paginated-list :list-array="notices"/>
  </div>
</template>

<script>
import http from "@/api/http"
import PaginatedList from "./PaginatedList.vue";

export default {
  components: {
    PaginatedList
  },
  data() {
    return { 
      notices: [],
      word:"",
      keys:[
        {value:"all", text:'--선택하세요--'},
        {value:"subject", text:'제목'},
      ],            //select option에 표시할 데이터 
      fields:[
        {key:"no", label:"질문 번호", tdClass: 'td1'},{key:"subject", label:"제목", tdClass: 'td2'},{key:"registerTime", label:"등록 시간", tdClass: 'td3'},
        {key:"hit", label:"조회수", tdClass: 'td4'},
      ],              //table의 header에 표시되는 데이터 
      key:"all",
    };
  },
  created() {
    this.searchNotice();
  },
  methods:{
    searchNotice(){
      console.log('key.......', this.key)
      const url =`notice?key=${this.key}&pageNo=1&word=${this.word}`;
      http.get(url)
          .then(({data})=>{
            console.log('응답 데이타', data)
            this.notices = data;
          })
    },
    
  },
};
</script>

<style scoped>
  .td1 {
    width: 10%;
  }
  .td2 {
    width: 40%;
  }
  .td3 {
    width: 40%;
  }
  .td4 {
    width: 10%;
  }
</style>
