<template>
  <div>
    <table class="table table-boardered">
      <tr>
        <td colspan="2">질문 내용</td>
      </tr>
      <tr>
        <td colspan="2">
          <textarea
            id="question"
            ref="question"
            v-model="qna.question"
          ></textarea>
        </td>
      </tr>
      <tr>
        <td colspan="2">
          <div class="text-center">
            <button class="btn btn-primary" @click="createHandler">등록</button>
            <button class="btn btn-primary" @click="moveHandler">목록</button>
          </div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import http from "@/api/http";
export default {
  data() {
    return {
      qna: {
        userid: this.$route.query.id,
        // userid:"ssafy@ssafy.com",
        question: "",
      },
    };
  },
  methods: {
    moveHandler() {
      this.$router.push({ name: "QnaListView", query:{id:this.userid} });
    },
    createHandler() {
      //데이타 검증
      let err = false;
      let msg = "";

      if (this.qna.question == "") {
        err = true;
        msg = "내용을 입력해 주세요";
        this.$refs.question.focus();
      }

      if (err) {
        alert(msg);
      } else {
        //등록 처리
        //localStorage에 이미 저장된 책 목록이 있는지 검사
        http.post("/qna", this.qna).then(({data})=>{
          if(data === "success") msg = "등록 성공!!!";
          alert("등록 완료");
          this.moveHandler();
        });

      }
    },
  },
};
</script>

<style scope>
  textarea {
    width: 100%;
		height: 200px;
		padding: 10px;
		box-sizing: border-box;
		/* border: solid 2px #1E90FF; */
		border-radius: 5px;
    border: none;
		/* font-size: 16px; */
		resize: none;
  }
</style>
