<template>
  <div>
    <table class="table table-boardered">
      <tr>
        <td>제목</td>
        <td>
          <input type="text" id="subject" ref="subject" v-model="notice.subject" />
        </td>
      </tr>
      <tr>
        <td colspan="2">공지 내용</td>
      </tr>
      <tr>
        <td colspan="2">
          <quill-editor
            class="box"
            ref="myTextEditor"
            v-model="notice.content"
            :disabled="false"

            :options="editorOption"
            @change="onEditorChange"
            @blur="onEditorBlur($event)"
            @focus="onEditorFocus($event)"
            @ready="onEditorReady($event)"
          />
        </td>
        <!-- <td colspan="2">
          <textarea
            id="content"
            cols="46"
            rows="10"
            ref="content"
            v-model="notice.content"
          ></textarea>
        </td> -->
      </tr>
      <tr>
        <td colspan="2">
          <div class="text-center">
            <button class="btn btn-primary" @click="createHandler">등록</button>
            <button class="btn btn-primary" @click="moveHandler">목록</button>
          </div>
        </td>
      </tr>
    </table>
    
    <!-- <div class="output ql-snow">
      <div v-html="content"></div>
    </div> -->
  </div>
</template>

<script>
import http from "@/api/http";

import hljs from "highlight.js";
import debounce from "lodash/debounce";
import { quillEditor } from "vue-quill-editor";

// highlight.js style
import "highlight.js/styles/tomorrow.css";

// import theme style
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";

export default {
  name: "quill-example-snow",
  title: "Theme: snow",
  components: {
    quillEditor,
  },

  data() {
    return {
      notice: {
        id:"admin",
        subject:"",
        content: "",
      },
      editorOption: {
        placeholder: "place holder test",
        modules: {
          toolbar: [
            ["bold", "italic", "underline", "strike"], // <strong>, <em>, <u>, <s>
            [{ header: 1 }, { header: 2 }], // <h1>, <h2>
            [{ list: "ordered" }, { list: "bullet" }],
            [{ script: "sub" }, { script: "super" }], // <sub>, <sup>
            [{ indent: "-1" }, { indent: "+1" }], // class제어
            [{ direction: "rtl" }], //class 제어
            [{ size: ["small", false, "large", "huge"] }], //class 제어 - html로 되도록 확인
            [{ header: [1, 2, 3, 4, 5, 6, false] }], // <h1>, <h2>, <h3>, <h4>, <h5>, <h6>, normal
            [{ font: [] }], // 글꼴 class로 제어
            [{ color: [] }, { background: [] }], //style="color: rgb(230, 0, 0);", style="background-color: rgb(230, 0, 0);"
            [{ align: [] }], // class
            // ["clean"],
          ],
          syntax: {
            highlight: (text) => hljs.highlightAuto(text).value,
          },
        },
      },
    };
  },
  methods: {
    moveHandler() {
      this.$router.push({ name: "ANoticeListView" });
    },
    createHandler() {
      //데이타 검증
      let err = false;
      let msg = "";

      if (this.notice.subject == "") {
        err = true;
        msg = "제목을 입력해 주세요";
        // $refs : Vue 객체에서 DOM 객체에 접근 할 때
        this.$refs.subject.focus();
      }

      if (!err && this.notice.content == "") {
        err = true;
        msg = "내용을 입력해 주세요";
        this.$refs.content.focus();
      }

      if (err) {
        alert(msg);
      } else {
        //등록 처리
        //localStorage에 이미 저장된 책 목록이 있는지 검사
        http.post("/notice", this.notice).then(({data})=>{
          if(data === "success") msg = "등록 성공!!!";
          alert("등록 완료");
          this.moveHandler();
        });

      }
    },
    onEditorChange: debounce(function(value) {
      this.content = value.html;
    }, 466),
    onEditorBlur(editor) {
      console.log("editor blur!", editor);
    },
    onEditorFocus(editor) {
      console.log("editor focus!", editor);
    },
    onEditorReady(editor) {
      console.log("editor ready!", editor);
    },
  },
  computed: {
    editor() {
      return this.$refs.myTextEditor.quill;
    },
    contentCode() {
      return hljs.highlightAuto(this.content).value;
    },
  },
  mounted() {
    console.log("this is Quill instance:", this.editor);
  },
};


</script>

<style>
  .outer {
    width:100%;
    text-align: center;
  }
  .box {
    margin: auto 0;
    display: inline-block;
  }
</style>
